import { observer } from "mobx-react-lite";
import React from "react";
import { useEffect } from "react";
import { useParams } from "react-router-dom";
import { Grid } from "semantic-ui-react";
import LoadingComponent from "../../../../app/layout/loadingComponent/LoadingComponent";
import { useStore } from "../../../../app/stores/store";
import ActivityDetailedChat from "../activityDetailedChat/ActivityDetailedChat";
import ActivityDetailedHeader from "../activityDetailedHeader/ActivityDetailedHeader";
import ActivityDetailedInfo from "../activityDetailedInfo/ActivityDetailedInfo";
import ActivityDetailedSidebar from "../activityDetailedSidebar/ActivityDetailedSidebar";

const ActivityDetails = () => {
  const { activityStore } = useStore();
  const {
    selectedActivity: activity,
    loadActivity,
    loadingInitial,
    clearSelectedActivity,
  } = activityStore;

  const { id } = useParams<{ id: string }>();

  useEffect(() => {
    if (id) loadActivity(id);
    return () => clearSelectedActivity();
  }, [id, loadActivity, clearSelectedActivity]);

  if (loadingInitial || !activity) return <LoadingComponent />;

  return (
    <Grid>
      <Grid.Column width={10}>
        <ActivityDetailedHeader activity={activity} />
        <ActivityDetailedInfo activity={activity} />
        <ActivityDetailedChat activityId={activity.id} />
      </Grid.Column>
      <Grid.Column width={6}>
        <ActivityDetailedSidebar activity={activity!} />
      </Grid.Column>
    </Grid>
  );
};

export default observer(ActivityDetails);
